// UserCart.js
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useRouter } from 'next/navigation';
import UserCartService from '../../_service/UserCartService';
import { createOrder } from '../../_service/OrderService';
import { fetchData } from '../../_lib/cartReducer';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Separator } from '@/components/ui/separator';
import { useToast } from '@/hooks/use-toast';
import CartTile from './CartTile'; // Assuming CartTile is in a separate file

const UserCart = () => {
  const dispatch = useDispatch();
  const userCartService = new UserCartService();
  const isLoggedIn = useSelector((state) => state.session.user);
  const cookie = Cookies.get('shopflow_session') ? JSON.parse(Cookies.get('shopflow_session')) : null;
  const router = useRouter();
  const products = useSelector((state) => state?.cart?.items) || [];
  const totalAmount = useSelector((state) => state?.cart?.totalAmount) || 0;
  const error = useSelector((state) => state.cart.error);
  const [loadingCheckout, setLoadingCheckout] = useState(false);
  const { toast } = useToast();

  useEffect(() => {
    const loadCart = async () => {
      if (isLoggedIn) {
        dispatch(fetchData(`/user/cart/viewCart?userId=${cookie?.user?.id}`, cookie?.token));
      }
    };

    loadCart();
  }, [isLoggedIn, dispatch]);

  const handleCheckout = async () => {
    setLoadingCheckout(true);
    try {
      const items = products.map((product) => ({
        productId: product.productId,
        quantity: product.quantity,
      }));
      const { order } = await createOrder(isLoggedIn.id, items);
      router.push(`/checkout/${order.id}`);
    } catch (err) {
      toast({ title: 'Error', description: 'Failed to create order. Please try again.', variant: 'destructive' });
    } finally {
      setLoadingCheckout(false);
    }
  };

  // Other handlers (increase, decrease, remove) remain the same...

  return (
    <div>
      {/* Render User Cart UI */}
      <h2 className="text-3xl font-semibold text-gray-800 mb-6">Shopping Cart</h2>
      {error ? (
        <p className="text-center text-red-500">{error.message}</p>
      ) : products.length === 0 ? (
        <p className="text-center text-gray-600">Your cart is empty</p>
      ) : (
        <div>
          {/* Render Cart Tiles */}
          {products.map((product, index) => (
            <CartTile
              key={index}
              product={product}
              // Pass necessary props
            />
          ))}
          {/* Price Details and Checkout Button */}
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">Price Details</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="flex justify-between">
                <p>Total Amount</p>
                <p>${totalAmount.toFixed(2)}</p>
              </div>
              <Button onClick={handleCheckout} disabled={loadingCheckout}>
                {loadingCheckout ? 'Processing...' : 'Checkout'}
              </Button>
            </CardContent>
          </Card>
        </div>
      )}
    </div>
  );
};

export default UserCart;